<template>
	<div id="app">

		<label>Project Name: <input type="text" v-model="projectName" /></label>
		<div v-for="(row, index) in rows" :key="index" class="breakerRow">
			<Breaker class="leftBreaker" :breaker="row.left"></Breaker>
			<Breaker class="rightBreaker" :breaker="row.right"></Breaker>
		</div>
	</div>
</template>

<script>
	import Breaker from './components/Breaker.vue';
	import localforage from 'localforage';

	export default {
		components: { Breaker },
		data()
		{
			return {
				projectName: "",
				rows: []
			};
		},
		created()
		{
			window.app = this;
			for (let i = 0; i < 20; i++)
				this.rows.push({ left: { text: "left" }, right: { text: "right" } });
		},
		watch:
		{
			rows: {
				deep: true,
				handler()
				{
					localforage.setItem('circuitBreakerPlanner_' + this.projectName, JSON.stringify(this.rows));
				}
			}
		}
	};
</script>

<style scoped>
	#app
	{
		font-family: Arial, Helvetica, sans-serif;
	}

	.breakerRow
	{
		display: flex;
	}
</style>

